body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #c9d6ff; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #e2e2e2,
    #c9d6ff
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#e2e2e2),
    to(#c9d6ff)
  );
  background: -webkit-linear-gradient(
    left,
    #e2e2e2,
    #c9d6ff
  );
  background: -o-linear-gradient(
    left,
    #e2e2e2,
    #c9d6ff
  );
  background: linear-gradient(
    to right,
    #e2e2e2,
    #c9d6ff
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.container {
  margin-top: 10px;
}
.input {
  width: 100%;
}
.footerContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background: #000;
  color: #fff;
  width: 100%;
  margin-top: auto;
}
.footerAnchor {
  text-decoration: none;
  color: #2e9797;
}



